*{
  font-family: 'Tenor Sans', sans-serif;
}
main {
  margin-top: 175px;
  margin-bottom:100px;
}
.App {
  text-align: center;
 
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.image {
  height:auto;
  min-height: 30vh;
  max-height: 80vh;
  width:inherit;
  max-width: 500px;
  text-align: center;
}
.card-body{
  width:100%;
  background: aliceblue;
}
.link-title{
  text-align: center;
}

.project-card{
  margin-bottom:10px;
}

.col-container {
  text-align: -webkit-center;
}
.myName{
  font-size: xxx-large !important;
}
.footer-container {
  justify-content: center !important;
}
.error {
  color:red
}

#resume-container {
  width: 100%;
  height:100%;
  min-height: 500px;
  text-align: center;
}
#resume-iframe {
  width: 90%;
  height:100%;
  min-height: 700px;
}
#aboutContainer{
  margin-top: 25px;
}
#aboutImage,#portrait, .navbar, #aboutCard {
  border-style: ridge;
  border-color: cornflowerblue;
}
#headerNav {
  border-top:none;
  border-right:none;
  border-left:none;
  border-width: 7.5px;
}
#footerNav {
  border-bottom:none;
  border-right:none;
  border-left:none;
  border-width: 7.5px;
}
#aboutCard {
  padding: 15px;
  border-width: 5px;
  background: aliceblue;
}
#aboutImage {
  height:auto;
  width: 100%;
  border-style: outset;
  border-color: cornflowerblue;
  
}
#aboutTitle {
  margin:5px;
  text-align: center;
  font-size: x-large;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-decoration: underline;
  text-decoration-color: cornflowerblue;
}

#aboutText{
  align-self: center;
}
#mission {
  text-align: center !important;
  font-size: x-large !important;
  font-weight:bolder;
}